.sidebar {
  width: 300px;
  display: flex;
  flex-direction: column;
  position: relative;
  height: 100vh;
  background: white;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  display: none;
}

.sidebar.open {
  display: block;
  position: absolute;
  z-index: 10;
}

.background {
  background-color: #00000030;
  height: 100%;
  width: 100%;
  position: absolute;
}

.nav {
  display: flex;
  flex-direction: column;
  margin-top: 70px;
  margin-left: 60px;
  font-weight: 600;
  font-size: 22px;
  gap: 30px;
}

.navLink {
  text-decoration: none;
  color: rgb(19, 19, 19);
}

.nav a:hover {
  color: #58acff;
  font-size: 20px;
}

.nav a.active {
  color: #0059b2;
  text-decoration: underline;
}
.close {
  position: absolute;
  top: 20px;
  right: 20px;
  background: transparent;
  border: none;
  cursor: pointer;
}

.close img {
  height: 20px;
}
