.button {
  border-radius: 3px;
  padding: 5px 24px;
  border: none;
  color: #fff;
  font-size: 14px;

  cursor: pointer;
}

.disabled {
  background: #004d99;
  color: #002e5c;
  opacity: 0.5;
}

.secondary {
  color: #0066cc;
  border: 1px solid #0066cc;
  background: transparent;
  border-radius: 3px;
}

.default {
  background: #0066cc;
}

.red {
  background: #cc1439;
  color: #fff;
}

.default:hover {
  background-color: #0080ff;
}

.red:hover {
  background: #ff1a47;
}

.secondary:hover {
  color: #0066cc;
  background: #e5f0fa;
}

.default:active {
  background: #0059b2;
}

.secondary:active {
  color: #0066cc;
  background: #c9e5ff;
}

.red:active {
  background: #b21232;
}

.button:disabled {
  background: #004d99;
  opacity: 50%;
}
